.tinder__cards {
  .tinderCards__cardsContainer {
    display: flex;
    justify-content: center;
    margin-top: 10vh;

    .swipe {
      position: absolute;

      .card {
        position: relative;
        background-color: #fff;
        width: 600px;
        padding: 20px;
        max-width: 85vw;
        height: 50vh;
        box-shadow: 0px 18px 53px 0px rgba(0, 0, 0, 0.3);
        border-radius: 20px;
        background-size: cover;
        background-position: center;

        h3 {
          position: absolute;
          bottom: 0;
          margin: 10px;
          color: #fff;
        }
      }
    }
  }
}